
.swiper-container {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  position: absolute;
}
.container {
  overflow: hidden;
}
.swiper-item {
  height: 100%;
}
.swiper-pagination-bullet-custom {
  width: 100% !important;
  -ms-flex: 1;
      flex: 1;
  height: 100% !important;
  margin: 0 !important;
  text-align: center;
  line-height: 3em;
  color: #999999;
  background-color: inherit;
  border-radius: 0 !important;
  opacity: 1 !important;
}
.swiper-pagination-bullet-custom.swiper-pagination-bullet-active {
  border-bottom: 2px solid transparent;
  background: inherit;
  background-color: inherit;
}
.swiper-pagination {
  position: absolute;
  top: 0;
  font-size: 1.25em;
  height: 3em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 100% !important;
  border-bottom: 1px solid #666;
}



