
@import url("//at.alicdn.com/t/font_0j6ycruegu7q4cxr.css");
* {
  box-sizing: border-box;
}
body {
  padding: 0;
  margin: 0;
  text-align: center;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: transparent;
  color: #2c3e50;
}
.mint-tabbar {
  height: 4em;
  box-shadow: 0 -1px 0px rgba(34, 25, 25, 0.4);
}
.mint-tabbar .mint-tab-item.is-selected {
    background-color: inherit;
    color: inherit;
}
.mint-tab-item {
  color: #999;
}
.mint-tab-item .mint-tab-item-label {
    font-size: 0.9em;
}
.mint-tab-item .mint-tab-item-icon {
    width: 2em;
    height: 2em;
    margin-bottom: 0.5em;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .3s;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}
.mask {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  z-index: 2;
  background-color: rgba(59, 62, 68, 0.95);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.mask .mask-img {
    -ms-flex: 1;
        flex: 1;
    background-color: rgba(59, 62, 68, 0.8);
}
.mask .mask-img img {
      width: 100%;
}
.iconfont {
  text-align: center;
  font-size: 2em;
}
a {
  text-decoration: none !important;
}
.mint-header {
  font-size: 1.5em;
  height: calc(2em * 1.25);
}
.mint-header-button .mint-button {
  height: 100%;
}
.mint-header-button .mint-button .mintui {
    font-size: 1em;
}



