
.mint-switch-core {
  width: 100%;
  height: 100%;
  border-radius: 1.5em;
  line-height: 1.5em;
}
.mint-switch-core:before {
    width: calc(100% - 2px);
    height: calc(100% - 2px);
    border-radius: calc(1.5em - 1px);
    margin-top: 1px;
    margin-left: 1px;
}
.mint-switch-core:after {
    width: calc(50% - 2px);
    height: calc(100% - 2px);
    border-radius: 50%;
    margin-top: 1px;
    margin-left: 1px;
}
.mint-switch-input:checked + .mint-switch-core::after {
  transform: translateX(calc(100% + 2px));
}



